@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;600&display=swap);
html,body{font-family:"Roboto", sans-serif;font-weight:300;letter-spacing:1px;background-color:#fff;color:#131313;transition:0.5s all}.height-100vh{min-height:100vh}::selection{background:#FFDC00;color:#131313}a{padding:4px;border-radius:4px;color:#fff;background-color:#8b46fa;transition:0.1s all}a:hover{text-decoration:none;color:#fff;background-color:#6200ff}.footer-credits{font-size:15px}.footer-link{font-size:15px;display:inline}.footer-link a{color:#6200ff;background-color:transparent !important}.footer-link a:hover{text-decoration:underline;color:#6200ff}ul{list-style-type:none;padding:0px}.intro{font-size:26px}.main-heading{color:#6200ff}.text-bold{font-weight:600}.project-tags li{display:inline;text-transform:capitalize}.btn-custom{border:1px solid #6200ff;padding:8px;font-size:14px}.carousel-control-prev,.carousel-control-next{margin-top:20px;height:30px;width:30px;border-radius:100%;transition:0.25s all;background-color:#fff;color:#6200ff}.carousel-control-prev:hover,.carousel-control-next:hover{color:#6200ff;background-color:#fff}.carousel-control-prev:focus,.carousel-control-next:focus{color:#6200ff}.carousel-indicators{margin-bottom:10px}.carousel-indicators li{background-color:#fff;height:10px;width:10px;border-radius:100%}.testimonial{background:#6200ff;background:linear-gradient(to bottom, #8b46fa, rgba(98,0,255,0.8));border-radius:8px}.testimonial-heading{font-size:16px}.testimonial-card{color:#fff;height:220px}.testimonial-card .name{font-size:16px}.testimonial-card .quote{font-size:14px}.testimonial-card .avatar{margin:0 auto;height:70px;width:70px;background-size:cover;border-radius:100%}

